<template>
  <div class="hs-container">
    <!-- <c-header :title="$t('base.Homsom')"></c-header> -->
    <hs-scroll
      ref="tableView"
      class="table-view"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_pullingDown">
      <ul class="bg" :style="{'background-image': `url('static/image/personal-center/person_bg.png')`}">
        <div class="header">
          <div class="logo" :style="{'background-image': `url('static/image/personal-center/head.png')`}"></div>
          <div class="des" v-if="details.UserInfo">
            <h2>{{details.UserInfo.Name}}</h2>
            <div class="prepayments" v-if="details.IsDisplayPrePayment" @click="$router.push('/advancePayment/center')">
              <div class="logo" :style="{'background-image': `url('static/image/personal-center/AdvanceCharge.png')`}"></div>
              <p>{{$t('base.Prepayments')}}&nbsp;&gt;</p>
            </div>
            <p>{{details.UserInfo.CompanyName}}</p>
            <p v-if="details.TcInfo" :type="language">{{$t('base.YourExclusiveTravelConsultant')}} - {{details.TcInfo.Name}}, {{$t('base.Telephone')}}: <span @click="_callPhone(details.TcInfo.Mobile)">{{details.TcInfo.Mobile}}</span></p>
          </div>
        </div>
        <div class="section" style="margin-top: 0px">
          <div class="section-header">{{$t('base.AllBookings')}}</div>
          <div class="order" :type="language" v-if="details.OrderMenuPermissions">
            <div
              class="item"
              v-if="details.OrderMenuPermissions.indexOf(11) >= 0"
              @click="$router.push('/planeTicket/orderList')">
              <div class="logo" :style="{'background-image': `url('static/image/personal-center/plane.png')`}"></div>
              <p>{{$t('base.DomesticFlights')}}</p>
            </div>
            <div
              class="item"
              v-if="details.OrderMenuPermissions.indexOf(31) >= 0"
              @click="$router.push('/intlPlaneTicket/orderList')">
              <div class="logo" :style="{'background-image': `url('static/image/personal-center/InterPlane.png')`}"></div>
              <p>{{$t('base.IntlFlights')}}</p>
            </div>
            <div
              class="item"
              v-if="details.OrderMenuPermissions.indexOf(21) >= 0"
              @click="$router.push('/hotel/orderList')">
              <div class="logo" :style="{'background-image': `url('static/image/personal-center/hotel.png')`}"></div>
              <p>{{$t('base.DomesticHotel')}}</p>
            </div>
            <div
              class="item"
               @click="$router.push('/inter-hotel/orderList')"
              v-if="details.OrderMenuPermissions.indexOf(41) >= 0">
              <div class="logo" :style="{'background-image': `url('static/image/personal-center/InterHotel.png')`}"></div>
              <p>{{$t('base.IntlHotel')}}</p>
            </div>
            <div
              class="item"
              v-if="details.OrderMenuPermissions.indexOf(51) >= 0"
              @click="$router.push('/train/orderInfoList')">
              <div class="logo" :style="{'background-image': `url('static/image/personal-center/train.png')`}"></div>
              <p>{{$t('base.Trains')}}</p>
            </div>
            <div
              class="item"
              v-if="details.OrderMenuPermissions.indexOf(13) >= 0"
              @click="$router.push('/personal/refundList')">
              <div class="logo" :style="{'background-image': `url('static/image/personal-center/cancelPlaneTicket.png')`}"></div>
              <p>{{$t('base.FlightsRefunds')}}</p>
            </div>
          </div>
        </div>
        <div class="section">
          <div class="item" @click="$router.push('/personal/setting')">
            <div class="logo" :style="{'background-image': `url('static/image/personal-center/center/setting-.png')`}"></div>
            <p style="width: calc(100% - 65px)">{{$t('base.Settings')}}</p>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
          <div class="item" @click="$router.push('/personal/aboutUs')">
            <div class="logo" :style="{'background-image': `url('static/image/personal-center/center/about-.png')`}"></div>
            <p style="width: calc(100% - 65px)">{{$t('base.AboutUs')}}</p>
            <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
        </div>
      </ul>
    </hs-scroll>
    <c-tabbar></c-tabbar>
  </div>
</template>
<script type="text/ecmascript-6">
// 个人中心
import CHeader from 'components/c-header/c-header'
import CTabbar from 'components/c-tabbar/c-tabbar'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import {kpiUserGetUserCenterInitInfo} from 'api'
import {mapMutations, mapActions} from 'vuex'
export default {
  components: {
    CHeader,
    CTabbar,
    HsScroll,
    HsSection,
    HsCell1
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {}
    }
  },
  methods: {
    ...mapMutations([
      'SET_PERSONAL_CENTER_DATA'
    ]),
    ...mapActions([
      'dispatchPersonalCenterData'
    ]),
    _getData() {
      this.dispatchPersonalCenterData().then((res) => {
        console.log('个人中心: ', res)
        this.details = res
      })
    },
    _pullingDown() {
      kpiUserGetUserCenterInitInfo({}).then((res) => {
        console.log('强制刷新个人中心: ', res)
        this.details = res.ResultData
        this.SET_PERSONAL_CENTER_DATA(res.ResultData)
      }).catch(() => {
        this.$refs.tableView.forceUpdate()
      })
    },
    _callPhone(mobile) {
      window.location.href = `tel:${mobile}`
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (from.path === '/' || from.path === '/home' || vm.noValue(JSON.stringify(vm.details))) {
        vm._getData()
      }
    })
  }
}
</script>
