              background-size 100% 100%
              margin-right 3px
            >p
              color $color_red_text
              font-size 0.7rem
          p
            color $color_white
            font-size 0.9rem
            line-height 1.2em
          p[type="EN"]
            font-size 0.8rem
      .section
        width calc(100% - 20px)
        margin-left 10px
        background-color $color_white
        border-radius 5px
        border 1px solid $color_gray_layer
        padding 5px 10px
        margin-top 10px
        >.section-header
          @extend .flex-row
          @extend .flex-start
          width 100%
          height 40px
          font-weight bold
          padding 0 5px
        >.order
          @extend .flex-row
          @extend .flex-start
          width 100%
          flex-wrap wrap
          >.item
            @extend .flex-column
            @extend .flex-center
            height 75px
            width 25%
            position relative
            >.logo
              width 30px
              height 30px
              background-size 100% 100%
            >p
              margin-top 8px
              font-size 0.8rem
              color $color_black_text_1
            >.mask
              @extend .flex-center
              @extend .flex-column
              position absolute
              top 0
              left 0
              width 100%
              height 100%
              background-color rgba(255, 255, 255, 0.5)
        >.order[type="EN"]
          >.item
            width 33.33%
            >p
              font-size 0.7rem
        >.item
          @extend .flex-row
          @extend .flex-start
          width 100%
          height 50px
          border-bottom 1px solid $color_gray_layer
          >.logo
            width 20px
            height 20px
            background-size 100% 100%
            margin-right 10px
          >p
            font-size 1rem
            width calc(100% - 80px)
            color $color_black_text_0
          >.badge
            @extend .flex-center
            width 20px
            height 20px
            border-radius 10px
            background-color $color_pink_background_1
            color $color_theme
          >.arrow
            width 20px
            height 20px
            background-size 100% 100%
            margin-left 5px
          &:last-child
            border-bottom-width 0px
    // .header
    //   @extend .flex-row
    //   @extend .flex-between
    //   width 100%
    //   background-color $color_white
    //   padding 20px 15px
    //   >.logo
    //     width 46px
